<template>
  <b-card
    :title="title"
    :img-src="image"
    img-alt="Image"
    img-top
    style="max-width: 20rem; cursor: pointer"
    @click="openUrl(projecturl)"
  >
    <b-card-text>
      {{ this.description }}
    </b-card-text>
  </b-card>
</template>

<script>
export default {
  name: "publishedProjectsSlideCard",
  props: ["title", "image", "description", "projecturl"],
  methods: {
    openUrl(url) {
      window.open(url);
    },
  },
};
</script>

