<template>
  <section :class="{ 'app-section': !hasDataLoaded }">
    <div id="app" v-if="hasDataLoaded">
      <div id="root" class="container">
        <nav class="navbar navbar-expand-md navbar-dark mb-3">
          <div class="collapse navbar-collapse" id="navbarColor01">
            <ul class="navbar-nav ml-auto">
              <li class="nav-item active"></li>
            </ul>
          </div>
        </nav>
        <div class="row">
          <sidebar />

          <div
            class="col-xs-12 col-md-9"
            style="border-left: 1px solid rgba(194, 194, 194, 0.1)"
          >
            <skills-section :skillsBadges="userData.skillsbadges" />

            <publishedProjectsSection
              :publishedProjects="userData.publishedProjects"
            />

            <experienceSection :experiences="userData.experiences" />

            <contact-form />
          </div>
        </div>
      </div>

      <footer class="footer">
        <p class="text-center mb-0 p-3">
          You can find this whole project on <a href="#">Github</a>. Vue © 2021
        </p>
      </footer>
    </div>
    <loader v-else></loader>
  </section>
</template>

<script>
import publishedProjectsSection from "@/components/publishedProjectsSection.vue";
import experienceSection from "@/components/experienceSection.vue";
import skillsSection from "@/components/skillsSection.vue";
import contactForm from "@/components/contactForm.vue";
import sidebar from "@/components/sidebar.vue";
import loader from "@/components/loader.vue";

export default {
  name: "App",
  data() {
    return {
      userData: {},
      hasDataLoaded: false,
    };
  },
  components: {
    publishedProjectsSection,
    experienceSection,
    skillsSection,
    contactForm,
    sidebar,
    loader,
  },
  created() {
    fetch("https://api.jsonbin.io/b/62241b4f06182767436e0ff4/2")
      .then((T) => T.json())
      .then((response) => {
        this.userData = response;
        this.hasDataLoaded = true;
      });
  },
};
</script>

