<template>
  <section id="publishedprojects">
    <h3 class="text-center title">Published projects</h3>
    <div class="row">
      <splide :options="options">
        <splide-slide
          v-for="publishedProject in publishedProjects"
          :key="publishedProject.title"
        >
          <publishedProjectsSlideCard
            :title="publishedProject.title"
            :image="publishedProject.image"
            :description="publishedProject.description"
            :projecturl="publishedProject.projecturl"
          />
        </splide-slide>
      </splide>
    </div>
  </section>
</template>

<script>
import publishedProjectsSlideCard from "@/components/publishedProjectsSlideCard.vue";

export default {
  name: "publishedProjectsSection",
  props: ["publishedProjects"],
  data() {
    return {
      options: {
        rewind: true,
        heightRatio: 0.69,
        perPage: 3,
        perMove: 1,
        gap: "1rem",
        padding: "3.3rem",
        focus: "center",
        breakpoints: {
          920: {
            perPage: 2,
          },
          450: {
            perPage: 1,
          },
        },
      },
    };
  },
  components: {
    publishedProjectsSlideCard,
  },
};
</script>

